.swiperImages {
  overflow: inherit !important;
  height: 654px;
  width: 100%;
  margin: 0 auto;
  background-color: #160643;
  margin-top: 60px;
  margin-bottom: 60px;


  .swiper-wrapper {


    &.singleImage {
      .swiper-slide {
        img {
          @media(max-width:$mobile-breakpoint) {
            object-fit: cover !important;
            margin-top: 60px;
          }

        }
      }
    }

    .swiper-slide-visible {
      opacity: 1;

      .slide-caption {
        position: relative;
        padding-top: 1rem;
        font-family: var(--global-font-abc-whyte);
        color: var(--global-color-gray-text);
        font-size: 17px;
        font-style: normal;
        font-weight: 400;
        line-height: 24px;
        letter-spacing: 0em;
        text-align: center;
        opacity: 1;
        min-height: 2rem;

        @media(max-width:$mobile-breakpoint) {
          font-size: 11px;
          line-height: 14px;
        }
      }
    }

    .swiper-slide {
      opacity: 1;

      .slide-caption {
        opacity: 1;
      }

      &:not(.swiper-slide-visible) {
        opacity: 0 !important;
      }
    }


    .swiper-slide img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: contain;

      @media(max-width:$mobile-breakpoint) {
        object-fit: cover !important;
      }
    }
  }


  .swiper-arrows {
    width: 215px;
    position: absolute;
    right: 0;
    top: calc(100% - 4rem);

    @media(max-width:$mobile-breakpoint) {
      width: 125px;
    }

    .swiper-button-next {

      background-color: transparent;
      color: #FFF;
      text-decoration: none;
      outline: none;
      cursor: pointer;
      transition: all 0.2s ease;
      width: 78px;
      height: 78px;
      top: auto;
      color: #FFF;
      border: 0;
      z-index: 1;
      right: 2rem;
      bottom: 2em;

      @media(max-width:$mobile-breakpoint) {
        width: 35px;
        height: 35px;
      }

      span {
        transition: transform 0.1s ease;
        position: relative;
        display: block;
      }

      &:before,
      &:after {
        border: 0;
        content: '';
        display: block;
        position: absolute;
        z-index: -1;
      }

      &:before {
        left: 0;
        top: 0;
        width: 78px;
        height: 78px;
        z-index: -1;
        background-color: #EF4136;
        background-image: url("../../dist/img/arrow-1.svg");
        background-repeat: no-repeat;
        background-size: inherit;
        background-position: center center;
        transition: all 0.1s ease;

        @media(max-width:$mobile-breakpoint) {
          width: 36px;
          height: 36px;
          background-size: 60%;
        }
      }


      // BOTTOM
      &:after {
        background-color: transparent;
        left: 8px;
        top: 8px;
        width: 74px;
        height: 74px;
        border: 2px solid #ABFFEB;
        transform: skewX(0deg);
        z-index: -2;

        @media(max-width:$mobile-breakpoint) {
          left: 5px;
          top: 5px;
          width: 31px;
          height: 31px;
        }
      }

      &:hover {
        background-color: transparent;
        box-sizing: content-box;

        span {
          transform: translate3d(8px, 8px, 0px);

          @media(max-width:$mobile-breakpoint) {
            transform: translate3d(5px, 5px, 0px);
          }
        }

        &:before {
          transform: translate3d(8px, 8px, 0px);

          @media(max-width:$mobile-breakpoint) {
            transform: translate3d(5px, 5px, 0px);
          }
        }
      }
    }

    .swiper-button-prev {

      background-color: transparent;
      color: #FFF;
      text-decoration: none;
      outline: none;
      cursor: pointer;
      transition: all 0.1s ease;
      width: 78px;
      height: 78px;
      top: auto;
      color: #FFF;
      border: 0;
      z-index: 1;
      right: 2rem;
      bottom: 2em;

      @media(max-width:$mobile-breakpoint) {
        width: 35px;
        height: 35px;
      }

      span {
        transition: transform 0.1s ease;
        position: relative;
        display: block;
      }

      &:before,
      &:after {
        border: 0;
        content: '';
        display: block;
        position: absolute;
        z-index: -1;
      }

      &:before {
        left: 0;
        top: 0;
        width: 78px;
        height: 78px;
        z-index: -1;
        background-color: #EF4136;
        background-image: url("../../dist/img/arrow-2.svg");
        background-repeat: no-repeat;
        background-size: inherit;
        background-position: center center;
        transition: all 0.2s ease;

        @media(max-width:$mobile-breakpoint) {
          width: 36px;
          height: 36px;
          background-size: 60%;
        }
      }


      // BOTTOM
      &:after {
        background-color: transparent;
        left: 8px;
        top: 8px;
        width: 74px;
        height: 74px;
        border: 2px solid #ABFFEB;
        transform: skewX(0deg);
        z-index: -2;

        @media(max-width:$mobile-breakpoint) {
          left: 5px;
          top: 5px;
          width: 31px;
          height: 31px;
        }
      }

      &:hover {
        background-color: transparent;
        box-sizing: content-box;

        span {
          transform: translate3d(8px, 8px, 0px);

          @media(max-width:$mobile-breakpoint) {
            transform: translate3d(5px, 5px, 0px);
          }
        }

        &:before {
          transform: translate3d(8px, 8px, 0px);

          @media(max-width:$mobile-breakpoint) {
            transform: translate3d(5px, 5px, 0px);
          }
        }
      }

    }

  }
}
