.partners-block-container{
  background-color: var(--global-color-dark-violet);
  width: 100%;
  .partners-block-content{
    max-width: $desktop-ml;
    margin: 0 auto;
    width: 100%;
    .partners-block-copy{
      font-family: var(--global-font-abc-whyte);
      color: #FFF;
      font-size: 21px;
      font-style: normal;
      font-weight: 400;
      line-height: 29px;
      letter-spacing: 0em;
      text-align: left;
      margin-bottom: 30px;
      margin-left: 10px;
      @media (max-width: $mobile-large) {
        margin: 0 auto;
        padding: 0 1rem;
       }
    }
    .partners-block-logos{
      display: flex;
      flex-wrap: wrap;
      flex-direction: row;
      justify-content: space-around;

      a,div{
        color: white;
        display: flex;
        flex-direction: column;
        align-content: center;
        justify-content: center;
        p{
          font-family: var(--global-font-abc-whyte);
          font-size: 22px;
          font-style: normal;
          font-weight: 400;
          line-height: 130%;
          color: white;
          letter-spacing: 0em;
          text-align: left;
          max-width: 170px;
          text-align: left;
        }

        img{
          height: 172px;
          width: auto;
          @media (max-width: $mobile-large) {
            height: 157px;
            width: auto;
          }
        }

      }
      
      
    

    }
  }
}


